<template>
  <div id="app" ref="app">
    <div :class="audioBoxClass">
      <music-audio></music-audio>
    </div>
    <router-view />
  </div>
</template>
<script>
import MusicAudio from "./components/Musicaudio";
import { createNamespacedHelpers } from "vuex";
const {
  mapState: musicState,
  mapActions: musicActions,
} = createNamespacedHelpers("MusicAudioStore");

export default {
  data() {
    return {
      audioBoxClass: "musicbox none",
    };
  },
  components: {
    MusicAudio,
  },
  methods: {
    setaudioBox() {
      if (this.clickSong == true) {
        if (this.detailbool == 0) {
          this.audioBoxClass = "musicbox tuijian";
        } else if (this.detailbool == 1) {
          this.audioBoxClass = "musicbox listdetail";
        } else {
          this.audioBoxClass = "musicbox none";
        }
      }
    },
  },
  watch: {
    detailbool(value) {
      this.setaudioBox();
    },
  },
  computed: {
    ...musicState(["detailbool", "clickSong"]),
  },
};
</script>
