import setupCookie from './assets/js/setupCookie'
Vue.prototype.setupCookie = setupCookie;
//设置基础请求路径
// axios.defaults.baseURL = 'http://localhost:3000/'
// axios.defaults.baseURL = 'http://81.68.67.75:3000/'
axios.defaults.baseURL = 'https://netease-cloud-music-api-phi.vercel.app/'
//全局注册过滤器
Vue.filter("formatDate", (value, format) => {
    let date = new Date(value);
    let o = {
        Y: date.getFullYear(),
        M: date.getMonth() + 1,
        D: date.getDate(),
    }
    for (let key in o) {
        let reg = new RegExp(`(${key}+)`)
        if (reg.test(format)) {
            let content = RegExp.$1;
            let text = o[key].toString().length >= 2 ? o[key].toString() : "0" + o[key].toString()
            format = format.replace(content, text)

        }
    }
    return format
})

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')