<template>
    <div class="musicaudio flexbetween">
        <audio
            controls
            :src="musicUrl"
            ref="audio"
            :data-id="audioData.id"
            @ended="setOnended"
        ></audio>
        <div class="ma-left flexbetween" @click="gosongdetail">
            <div class="musicimg">
                <img v-if="audioData.picUrl" :src="audioData.picUrl" alt />
                <img v-else src="../assets/img/tj.png" alt />
            </div>
            <div class="musicname">
                <span>{{ audioData.name ? audioData.name : "网易云" }}</span>
            </div>
        </div>
        <div class="ma-right flexbetween">
            <div class="clickbtn" @click="clickIcon">
                <img :src="iconName" alt />
            </div>
            <div class="listbtn" is-link @click="showPopup">
                <img src="../assets/img/musiclist.svg" alt />
            </div>
            <van-popup
                round
                position="bottom"
                :style="{ height: '64%' }"
                v-model="show"
                @opened="popupOpened"
                @close="popupClose"
            >
                <div class="title flexbetween">
                    <div class="titleLeft">
                        <span>当前播放</span>
                        <span class="num">({{ listLength }})</span>
                    </div>
                    <div class="titleRgiht" @click="switchBtn">
                        <span>{{ playStyleText }}</span>
                        <img :src="newShowBtn" alt />
                    </div>
                </div>
                <div class="listBody">
                    <div
                        class="listBodyItem flexbetween"
                        :class="item.isActive ? 'active' : ''"
                        v-for="(item, index) in songListData"
                        :key="index"
                    >
                        <div
                            class="listBodyItem_left one-ellipsis"
                            @click="clickSongListItem(item, index)"
                        >
                            <div class="text">
                                <span class="songName">{{ item.name }}</span>
                                <span class="songHen">-</span>
                                <span class="songAuther">{{
                                    item.ar[0].name
                                }}</span>
                            </div>
                        </div>
                        <div class="listBodyItem_right" @click="delSong(index)">
                            <img src="../assets/img/close.svg" alt />
                        </div>
                    </div>
                </div>
            </van-popup>
        </div>
    </div>
</template>

<script>
import "../assets/less/musicaudio.less";
import { Notify } from "vant";
import { createNamespacedHelpers } from "vuex";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    name: "Musicaudio",

    computed: {
        ...musicState([
            "musicUrl",
            "audioData",
            "iconNum",
            "songListData",
            "playStyleNum"
        ])
    },
    created() {
        this.$nextTick(() => {
            this.setAudio(this.$refs.audio);
        });
    },
    data() {
        return {
            show: false,
            iconData: ["play", "suspend"],
            iconName: require("../assets/img/play.svg"),
            //播放歌曲数据
            // thisSongListData: [],
            //播放列表歌曲数目
            listLength: 0,
            //播放类型按钮数据
            btnData: ["xh", "onexh", "sj"],
            //当前播放类型按钮
            newShowBtn: require("../assets/img/xh.svg"),
            //当前播放类型文字
            playStyleText: "列表循环",
            playStyleTextData: ["列表循环", "单曲循环", "随机播放"]
        };
    },

    methods: {
        ...musicActions([
            "setAudio",
            "seticonNum",
            "setsongData",
            "setPlayStyleNum"
        ]),

        //点击播放
        clickIcon() {
            if (this.iconNum == 0) {
                this.seticonNum(1);
                this.$refs.audio.play();
            } else {
                this.seticonNum(0);
                this.$refs.audio.pause();
            }
        },

        // 设置vip播放按钮
        setvipBtn() {
            let _this = this;
            this.$refs.audio.addEventListener("error", function () {
                Notify({
                    message: "请升级到黑胶vip",
                    color: "#000",
                    background: "#ee2e23"
                });
                _this.seticonNum(0);
            });
            this.$toast.clear();
        },

        //跳转
        gosongdetail() {
            this.$router.push({ name: "Songdetail" });
        },

        //点击显示播放歌单
        showPopup() {
            this.show = true;
            // 设置播放列表数量
            this.listLength = this.songListData.length;
            let songId = this.$refs.audio.getAttribute("data-id");
            this.songListData.map(v => {
                if (v.id == songId) {
                    v.isActive = true;
                } else {
                    v.isActive = false;
                }
            });
        },

        //点击歌单的歌曲
        clickSongListItem(item, index) {
            if (item.isActive) {
                return;
            }
            for (let i = 0; i < this.songListData.length; i++) {
                if (this.songListData[i].isActive) {
                    this.songListData[i].isActive = false;
                    break;
                }
            }
            item.isActive = true;
            this.setsongData(item).then(audio => {
                audio.play();
            });
            this.seticonNum(1);
        },

        //点击删除
        delSong(index) {
            // 设置播放列表数量
            this.listLength = --this.listLength;

            //随机
            if (this.playStyleNum == 2) {
                let randomNum = Math.ceil(Math.random() * this.listLength - 1);
                for (let i = 0; i < this.songListData.length; i++) {
                    if (this.songListData[i].isActive) {
                        if (i == index) {
                            this.setsongData(this.songListData[randomNum]).then(
                                audio => {
                                    audio.play();
                                }
                            );
                            this.songListData[randomNum].isActive = true;
                        } else {
                        }
                    }
                }
            } else {
                //其他
                for (let i = 0; i < this.songListData.length; i++) {
                    if (this.songListData[i].isActive) {
                        if (i == index) {
                            this.setsongData(this.songListData[i + 1]).then(
                                audio => {
                                    audio.play();
                                }
                            );
                            this.songListData[i + 1].isActive = true;
                        }
                    }
                }
            }

            this.songListData.splice(index, 1);
        },

        //设置弹出框的title
        popupOpened() {
            document.querySelector(".title").style.position = "fixed";
        },

        //设置弹出框的title
        popupClose() {
            document.querySelector(".title").style.position = "static";
        },

        //点击切换播放模式
        switchBtn() {
            let setNum = this.playStyleNum + 1;
            if (setNum >= this.btnData.length) {
                setNum = 0;
                this.setPlayStyleNum(0);
            }
            this.playStyleText = this.playStyleTextData[setNum];
            this.newShowBtn = require("../assets/img/" +
                this.btnData[setNum] +
                ".svg");
            this.setPlayStyleNum(setNum);
        },

        //自动播放结束后的播放方式
        setOnended() {
            // 无播放列表
            if (this.songListData.length == 0) {
                this.seticonNum(0);
            }
            //设置自动播放结束播放形式
            //设置单曲循环
            let songId = this.$refs.audio.getAttribute("data-id");
            if (this.playStyleNum == 1) {
                this.seticonNum(1);
                this.$refs.audio.play();
            }
            //设置列表循环
            else if (this.playStyleNum == 0) {
                for (let i = 0; i < this.songListData.length; i++) {
                    if (songId == this.songListData[i].id) {
                        let nowPlay = 0;
                        if (i + 1 == this.songListData.length) {
                            nowPlay = 0;
                        } else {
                            nowPlay = i + 1;
                        }
                        this.setsongData(this.songListData[nowPlay]);
                        this.songListData[nowPlay].isActive = true;
                    } else {
                        this.songListData[i].isActive = false;
                    }
                }
            } //随机
            else if (this.playStyleNum == 2) {
                for (let i = 0; i < this.songListData.length; i++) {
                    if (songId == this.songListData[i].id) {
                        let nowPlay = Math.ceil(
                            Math.random() * this.songListData.length - 1
                        );
                        this.setsongData(this.songListData[nowPlay]);
                        this.songListData[nowPlay].isActive = true;
                    } else {
                        this.songListData[i].isActive = false;
                    }
                }
            }
        }
    },

    beforeUpdate() {
        // 设置vip播放按钮
        this.setvipBtn();
    },

    //监听
    watch: {
        iconNum(val) {
            this.iconName = require("../assets/img/" +
                this.iconData[val] +
                ".svg");
            if (val == 0) {
                this.$refs.audio.pause();
            } else {
                let this_ = this;
                this.$refs.audio.oncanplay = function () {
                    this_.$refs.audio.play();
                };
            }
        }
    }
};
</script>
