import VueCookies from 'vue-cookies'
class SetupCookie {
    constructor() {
        // this.num = 0
        ////
        this.num = 9
    }

    //设置cookies
    setupCookies(cookies) {
        let cookiesArray = cookies.split(";")
        this.num = cookiesArray.length;

        let o = {
            //混淆
            "msjt": "Expires=Fri, 25-Sep-2020 03:37:53 GMT"
        }
        for (let key in cookiesArray) {
            o["ms" + key + "1a"] = cookiesArray[key]
        }
        for (let k in o) {
            VueCookies.set(k, o[k], "10d")
        }
    }

    //获取cookies
    getCookies() {
        let cookiesString = ""
        for (let i = 0; i < this.num; i++) {
            cookiesString += VueCookies.get("ms" + i + "1a") + ";";
        }

        return cookiesString.slice(0, -1)

    }

    //删除cookies
    removeCookies() {
        for (let i = 0; i < this.num; i++) {
            VueCookies.remove("ms" + i + "1a");
        }
    }
}
export const setupCookie = new SetupCookie();