export const actions = {
    setAudio({ state }, data) {
        state.audio = data
    },
    //设置用户id
    setUserId({ state }, data) {
        state.userId = data
    },

    //设置音乐组件位置
    setaudioxy({ state }, data) {
        state.detailbool = data
    },
    //设置iconnum
    seticonNum({ state }, data) {
        state.iconNum = data;
    },
    //设置歌曲信息
    setsongData({ state }, data) {
        state.songData = [];
        state.musicUrl = "";
        state.audioData.id = "";
        state.audioData.name = "";
        state.audioData.picUrl = "";
        state.songWordData = [];
        state.songData = data;
        state.musicUrl = data.palyurl;
        state.audioData.id = data.id;
        state.audioData.name = data.name;
        state.audioData.picUrl = data.al.picUrl;
        console.log('data.songWordData :>> ', data.songWordData);
        state.songWordData = data.songWordData;
        return state.audio;

    },
    //设置热评墙的页码
    setHotWallPageNum({ state }, data) {
        state.hotWallPageNum = data
    },

    //设置歌单
    setSongListData({ state }, data) {
        state.songListData = data
    },

    //设置播放形式
    setPlayStyleNum({ state }, data) {
        state.playStyleNum = data
    },

    //设置切歌判断
    setCutSongBool({ state }, data) {
        state.cutSongBool = data
    },

    //设置歌词
    // setSongWordData({ state }, data) {

    //     state.songWordData = data;
    // }

    // 设置歌单数据缓存
    setSongListCache({ state }, data) {
        state.songListCache = data;
    },

    //设置是否点击了歌曲的判断值
    setClickSong({ state }, data) {
        console.log('歌曲的判断值 :>> ', data);
        state.clickSong = data;
    },


}