export const routes = [{
        path: '/main',
        name: 'Main',
        component: () =>
            import('../views/Main.vue'),
        children: [{
                path: 'tuijian',
                name: 'Tuijian',
                component: () =>
                    import('../views/mainviews/Tuijian.vue')
            },
            {
                path: 'videopage',
                name: 'Videopage',
                component: () =>
                    import('../views/mainviews/Videopage.vue')
            },
            {
                path: 'find',
                name: 'Find',
                component: () =>
                    import('../views/mainviews/Find.vue')
            },
            {
                path: 'mymusic',
                name: 'Mymusic',
                component: () =>
                    import('../views/mainviews/Mymusic.vue')

            },
            {
                path: '*',
                redirect: {
                    name: "Tuijian"
                }
            }
        ]
    }, {
        path: '/login',
        name: 'Login',
        component: () =>
            import('../views/Login.vue'),
    }, {
        path: '/listdetails',
        name: 'Listdetails',
        component: () =>
            import('../views/Listdetails.vue'),
    },
    {
        path: '/lyrics',
        name: 'Lyrics',
        component: () =>
            import('../views/Lyrics.vue'),
    },
    {
        path: '/songdetail',
        name: 'Songdetail',
        component: () =>
            import('../views/Songdetail.vue'),
    },
    {
        path: '/hotword',
        name: 'Hotword',
        component: () =>
            import('../views/Hotword.vue'),
    },
    {
        path: '*',
        redirect: {
            name: "Tuijian"
        }
    }


]